/* Housing Card */

.card-link {
	display: block;
	text-decoration: none;
	color: white;
	background: linear-gradient(
			180deg,
			rgba(255, 255, 255, 0) 0%,
			rgba(0, 0, 0, 0.5) 100%
		),
		$main-color;
	border-radius: 10px;
	height: 100%;
}

.main-card {
	padding: 20px;
	min-height: 340px;
	height: 100%;

	@include for-device(mobile) {
		min-height: 255px;
	}

	&__img {
		height: 240px;

		@include for-device(mobile) {
			height: 165px;
		}

		img {
			border-radius: 10px;
			height: 100%;
			width: 100%;
			object-fit: cover;
		}
	}

	&__title {
		padding-top: 20px;
		height: calc(100% - 240px);
		display: flex;
		align-items: center;

		@include for-device(mobile) {
			height: calc(100% - 165px);
		}

		h2 {
			@include font-set(1.125em, 500);
		}
	}
}
