/*Loading spinner*/

.loading-spinner {
	height: 600px;
	position: relative;

	&::after {
		content: "";
		width: 100px;
		height: 100px;
		border-radius: 50%;
		position: absolute;
		top: 100px;
		left: calc(50% - 50px);
		background: conic-gradient($bg-color, $main-color);
		animation: spinner-kf linear 1s infinite both;
	}

	&__center {
		display: block;
		background-color: white;
		border-radius: 50%;
		width: 90px;
		height: 90px;
		position: absolute;
		top: 105px;
		left: calc(50% - 45px);
		z-index: 1;
	}
}

@keyframes spinner-kf {
	100% {
		transform: rotate(360deg);
	}
}
